.App {
  height: 100svh;
  width: 100svw;
  display: flex;
  justify-content: center;
  /* background: url('./assets/images/bg-light.png') no-repeat center fixed; */
  /* background-size: cover; */
  overflow-y: scroll;
}

.App > img {
  position: absolute;
  height: inherit;
  width: inherit;
  top: 0;
  left: 0;
}

body, html {
  font-family: 'Noto Sans';
  font-weight: 400;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.content {
  display: flex;
  max-width: 736px;
  flex: 1;
}
